.card {
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  background-color: var(--color-white);
  padding: 32px 16px 16px;
}

.card__image {
  display: block;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  line-height: 18px;
  font-weight: 500;
  color: var(--color-dark);
}

.card__product-code {
  margin-top: 0;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: var(--color-light-grey);
}

.card__stars-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.card__reviews {
  color: var(--color-dark);
}

.card__price-title {
  margin: 0;
  font-size: 12px;
  line-height: 18px;
  display: flex;
  align-items: flex-end;
  color: var(--color-light-grey);
}

.card__price-value {
  margin: 0;
  color: var(--color-dark);
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
}

.card__price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.card__link {
  width: 100%;
  height: 40px;
  background-color: var(--color-blue);
  border-radius: 5px;
  text-decoration: none;
  color: var(--color-white);
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--color-blue);
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 700;
}

.card__link:hover {
  background-color: var(--color-white);
  color: var(--color-blue);
}
